import React from 'react'
import {Redirect} from "react-router";
import {connect} from "react-redux";

let StateTo=(state)=>({ state: state.LoginReducer });

export const withAuthRedirect=(Component)=>{

    class RediComp extends React.Component{

        render() {
                return (
                        this.props.state.userEntry.status
                        ? <Component {...this.props} />
                        : <Redirect to={`/` }/>
                        )
        }
    }
    let ConnectedAuth= connect(StateTo)(RediComp);

    return ConnectedAuth
};

