import React from "react";
import {NavLink} from "react-router-dom";

let Subscriber = (props) => {
    debugger
    let pageCount;
    let pages = [];

    if (props.allPeople!==0 && props.allPeople){
        pageCount = Math.ceil(props.allPeople / props.pageSize);
        pages = [];
        pageCount = pageCount + 1;
        for (let i = 1; i < pageCount; i++) {
            pages.push(i);
        }
    }

    return (
      <div className={"SUBSCRIBERS"}>

            <div className={"LIST_PAGE"}>
                 {

                  pages.map(p =>{
                      debugger
                      return (
                          <span onClick={(e)=>props.clic(p)}
                                id={props.currentPage === p && 'ter'}
                          >
                              {p}
                          </span>
                      )})
                 }
            </div>


            <div className='Users'>
                   {
                     props.users.map(z =>(
                        <div key={z.id}>
                            <div className='Users_item'>
                                   <div className="UserID">
                                      {
                                        z.friend
                                        ? <div className="PicSub"><button onClick={() => {props.Subscribe(false , z.id)}}><img src={z.fotto} alt="Pic"/></button></div>
                                        : <div className="PicSubi"><button onClick={() => {props.Subscribe(true , z.id)}}><img src={z.fotto} alt="Pic"/></button></div>
                                      }
                                          <div className='FuNam'>
                                            <div>
                                                <div><NavLink to={'/profile/' + z.id}>{z.name} {z.lastName}</NavLink></div>
                                                <div><NavLink to={'/profile/'+z.id}>Я скажу так:"{z.statuss}"</NavLink></div>
                                            </div>
                                            <div>
                                                <div>{z.country}</div>
                                                <div>{z.city}</div>
                                            </div>
                                          </div>
                                   </div>
                            </div>
                        </div>
                     ))
                   }
            </div>
      </div>
    )
}
export default Subscriber;